.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.calculator {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 400px;
  height: 622px;
  background-color: #cee2f3;
  border: 1px solid #000105;
}

.container {
  width: 330px;
  height: 550px;
}

.vueResult {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;

  width: 331px;
  height: 113px;
  margin: 0 auto;
  margin-bottom: 25px;

  background-color: #fff;

  border: 1px solid #000105;

  .currentInput {
    text-align: right;
    color: #000;
    margin-top: 28px;
    font-size: 28px;
    padding: 0px;
    // letter-spacing: 6px;
  }

  .resultOutput {
    color: #bababa;
    margin-right: 25px;
    margin-bottom: 25px;
    font-size: 20px;
    text-align: right;
  }
}

.clearStyleInput {
  width: 306px;
  padding: 0;
  margin: 0;
  border: 0;
  border: none;
  background-color: #fff;
}

.wrapper {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}

.blockButton {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 329px;
  height: 412px;
  margin: 0 auto;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 66px;
  height: 63px;
  border: 1px solid #000105;
  background-color: #fff;
  color: #030303;

  &:nth-child(4n+4) {
    border: 2px solid #c2a070;
  }

  &_large {
    width: 155px;
    background-color: #ff9900;
  }
}

.bcAC {
  background-color: #cdccd1;
}

.colorTextButton {
  background-color: #787878;
}

.btn__text {
  margin: auto;

  &_black {
    color: #0f0f0f;
  }

  &_gray {
    color: #575757;
  }
}